"use client"

import { useState, useRef, useEffect } from "react"
import { MessageSquare, X, Send, Bot, User } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { cn } from "@/lib/utils"
import { companyInfo } from "@/lib/data/company"
import { products } from "@/lib/data/products"
import { serviceDetails } from "@/lib/data/serviceDetails"

interface Message {
  id: string
  role: "user" | "bot"
  content: string
  timestamp: Date
}

export function Chatbot() {
  const [isOpen, setIsOpen] = useState(false)
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      role: "bot",
      content:
        "Hello! I am the A V Tech Assistant. How can I help you today?\n\nYou can ask me about:\n‚Ä¢ Products\n‚Ä¢ Services\n‚Ä¢ Contact Information\n‚Ä¢ Company Details",
      timestamp: new Date(),
    },
  ])

  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages, isOpen])

  // Quick Reply Buttons --------------------------
  const quickReplies = [
    { label: "Products", value: "Show me products" },
    { label: "Services", value: "Services list" },
    { label: "Contact", value: "Contact information" },
    { label: "Company Info", value: "Tell me about the company" },
  ]

  const sendQuickReply = (value: string) => {
    setInputValue(value)
    handleSendMessage(value)
  }

  const handleSendMessage = async (manualText?: string) => {
    const text = manualText || inputValue
    if (!text.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: text,
      timestamp: new Date(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInputValue("")
    setIsTyping(true)

    setTimeout(() => {
      const botResponse = generateResponse(text)
      setMessages((prev) => [
        ...prev,
        {
          id: (Date.now() + 1).toString(),
          role: "bot",
          content: botResponse,
          timestamp: new Date(),
        },
      ])
      setIsTyping(false)
    }, 600)
  }

  // ------------- RESPONSE GENERATOR ---------------------
  const generateResponse = (query: string): string => {
    const lower = query.toLowerCase()

    // Product Details
    const matchingProduct = products.find(
      (p) =>
        lower.includes(p.name.toLowerCase()) ||
        lower.includes(p.id.toLowerCase())
    )

    if (matchingProduct) {
      return `**${matchingProduct.name}**\n${matchingProduct.fullDescription}\n\nFeatures:\n‚Ä¢ ${matchingProduct.features.join(
        "\n‚Ä¢ "
      )}\n\nApplications:\n‚Ä¢ ${matchingProduct.applications.join("\n‚Ä¢ ")}`
    }

    if (lower.includes("product") || lower.includes("panel")) {
      return (
        "Here are our main products:\n" +
        products.map((p) => `‚Ä¢ ${p.name}`).join("\n") +
        "\n\nTell me which product you want details for!"
      )
    }

    // Services Logic
    const matchingService = services.find((s) =>
      lower.includes(s.name.toLowerCase())
    )

    if (matchingService) {
      return `**${matchingService.name}**\n${matchingService.description}`
    }

    if (lower.includes("service") || lower.includes("support")) {
      return (
        "Here are our available services:\n" +
        services.map((s) => `‚Ä¢ ${s.name}`).join("\n") +
        "\n\nTell me which service you want details for."
      )
    }

    // Contact Info
    if (
      lower.includes("contact") ||
      lower.includes("email") ||
      lower.includes("phone") ||
      lower.includes("address")
    ) {
      return `üìç **A V Tech Contact Information**\n\n**Phone:** ${companyInfo.contact.phone}\n**Email:** ${companyInfo.contact.emails[0]}\n**Office:** ${companyInfo.address.office}\n**Works:** ${companyInfo.address.factory}`
    }

    // About Company
    if (lower.includes("about") || lower.includes("company")) {
      return companyInfo.about
    }

    return "I'm not sure I understand. You can ask me about our products, services, or contact information."
  }

  return (
    <>
      {/* Toggle Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={cn(
          "fixed bottom-6 right-20 bg-primary text-white p-3 rounded-full shadow-lg hover:scale-105 transition-all",
          isOpen ? "hidden" : "flex"
        )}
      >
        <MessageSquare className="h-6 w-6" />
      </button>

      {/* Chat Window */}
      <div
        className={cn(
          "fixed bottom-24 right-6 w-80 md:w-96 bg-white rounded-2xl shadow-2xl border border-gray-200 flex flex-col transition-all",
          isOpen ? "scale-100 opacity-100" : "scale-0 opacity-0 pointer-events-none"
        )}
        style={{ maxHeight: "500px" }}
      >
        {/* Header */}
        <div className="bg-primary p-4 flex justify-between items-center text-white">
          <div className="flex items-center gap-2">
            <Bot className="h-5 w-5" />
            <h3 className="font-bold text-sm">A V Tech Assistant</h3>
          </div>
          <button onClick={() => setIsOpen(false)} className="text-white/80 hover:text-white">
            <X className="h-5 w-5" />
          </button>
        </div>

        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-4 bg-gray-50" ref={scrollRef}>
          <div className="space-y-4">
            {messages.map((msg) => (
              <div
                key={msg.id}
                className={cn("flex gap-2", msg.role === "user" ? "ml-auto flex-row-reverse" : "")}
              >
                <div
                  className={cn(
                    "w-8 h-8 rounded-full flex items-center justify-center",
                    msg.role === "user" ? "bg-gray-200" : "bg-primary/10 text-primary"
                  )}
                >
                  {msg.role === "user" ? <User /> : <Bot />}
                </div>
                <div
                  className={cn(
                    "p-3 rounded-2xl text-sm shadow-sm whitespace-pre-line",
                    msg.role === "user"
                      ? "bg-primary text-white rounded-tr-none"
                      : "bg-white border rounded-tl-none"
                  )}
                >
                  {msg.content}
                </div>
              </div>
            ))}

            {/* Typing Indicator */}
            {isTyping && (
              <div className="flex gap-2">
                <div className="w-8 h-8 rounded-full bg-primary/10 text-primary flex items-center justify-center">
                  <Bot />
                </div>
                <div className="bg-white p-3 rounded-2xl border shadow-sm flex gap-1">
                  <span className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></span>
                  <span className="w-2 h-2 bg-gray-400 rounded-full animate-bounce delay-150"></span>
                  <span className="w-2 h-2 bg-gray-400 rounded-full animate-bounce delay-300"></span>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Quick Replies */}
        <div className="p-2 flex gap-2 overflow-x-auto bg-white border-t">
          {quickReplies.map((q) => (
            <Button
              key={q.value}
              onClick={() => sendQuickReply(q.value)}
              className="whitespace-nowrap bg-secondary text-black border"
              variant="outline"
            >
              {q.label}
            </Button>
          ))}
        </div>

        {/* Input */}
        <div className="p-3 bg-white border-t">
          <form
            onSubmit={(e) => {
              e.preventDefault()
              handleSendMessage()
            }}
            className="flex gap-2"
          >
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder="Ask something..."
            />
            <Button type="submit" size="icon" className="bg-primary">
              <Send />
            </Button>
          </form>
        </div>
      </div>
    </>
  )
}
